<template>
  <div class="detail-wrap">
    <div class="detail-left">
      <div class="product-board">
        <img :src="productIcon">
        <ul>
         <router-link v-for="item in products" :to="{ path: item.path }" tag="li" active-class="active">
            {{ item.name }}
         </router-link>
        </ul>
      </div>
    </div>
    <div class="detail-right">
      <keep-alive>
        <router-view :huangXian="huangXian" :XG="XG" :WQ="WQ"></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script>
  import analysis from './detail/analysis'
  export default {
    components: {
      analysis
    },
    data () {
      return {
        products: [
          {
            name: '天然硫磺温泉',
            path: 'count',
            icon: require('../assets/images/1.png'),
            active: false
          },
          {
            name: '黄仙洞',
            path: 'forecast',
            active: false
          },
          {
            name: '香菇木耳',
            path: 'analysis',
            active: false
          },
          {
            name: '葛粉',
            path: 'publish',
            active: false
          },
          {
            name: '云雾茶',
            path: 'tea',
            active: false
          }
        ],
        huangXian: [
          {
            src: require('../assets/huangxian/cp1.jpg'),
            title: '001'
          },
          {
            src: require('../assets/huangxian/cp2.jpg'),
            path: 'analysis',
            title: '002'
          },
          {
            src: require('../assets/huangxian/cp3.jpg'),
            title: '003'
          }
        ],
        XG: [
          {
            src: require('../assets/huangxian/timg.jpg'),
            title: '001'
          },
          {
            src: require('../assets/huangxian/timg3.jpg'),
            title: '002'
          },
          {
            src: require('../assets/huangxian/timg2.jpg'),
            title: '003'
          }
        ],
        WQ: [
          {
            src: require('../assets/huangxian/i.jpg'),
            title: '001'
          },
          {
            src: require('../assets/huangxian/i3.jpg'),
            title: '002'
          },
          {
            src: require('../assets/huangxian/i2.jpg'),
            title: '003'
          },
          {
            src: require('../assets/huangxian/i4.jpg'),
            title: '003'
          },
          {
            src: require('../assets/huangxian/i5.jpg'),
            title: '003'
          },
          {
            src: require('../assets/huangxian/i6.jpg'),
            title: '003'
          }
        ],
        imgMap: {
          '/detail/count': require('../assets/images/1.png'),
          '/detail/forecast': require('../assets/images/2.png'),
          '/detail/analysis': require('../assets/images/3.png'),
          '/detail/publish': require('../assets/images/4.png'),
          '/detail/tea': require('../assets/images/5.jpg')
        }
      }
    },
    computed: {
      productIcon () {
        return this.imgMap[this.$route.path]
      }
    }
  }
</script>

<style>
  .detail-wrap {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
    padding-top: 20px;
  }
  .detail-left {
    float: left;
    width: 200px;
    text-align: center;
  }
  .detail-right {
    float: left;
    width: 980px;
    margin-left: 20px;
  }
  .product-board {
    background: #fff;
    padding: 20px 0;
  }
  .product-board ul {
    margin-top: 20px;
  }
  .product-board li {
    text-align: left;
    padding: 10px 15px;
    cursor: pointer;
  }
  .product-board li.active,
  .product-board li:hover {
    background: #4fc08d;
    color: #fff;
  }
  .product-board li a {
    display: block;
  }
  .sales-board {
    background: #fff;
  }
  .sales-board-form {

  }
  .sales-board-intro h2 {
    font-size: 20px;
    padding: 20px;
  }
  .sales-board-intro p {
    background: #f7fcff;
    padding: 10px 20px;
    color: #999;
    line-height: 1.8;
  }
  .sales-board-form {
    padding: 30px 20px;
    font-size: 14px;
  }
  .sales-board-line {
    clear: both;
    padding-bottom: 20px;
  }
  .sales-board-line-left {
    display: inline-block;
    width: 100px;
  }
  .sales-board-line-right {
    display: inline-block;
    width: 75%;
  }
  .sales-board-des {
    border-top: 20px solid #f0f2f5;
    padding: 15px 20px;
  }
  .sales-board-des p {
    line-height: 1.6;
  }
  .sales-board-des h2 {
    font-size: 20px;
    padding-bottom: 15px;
  }
  .sales-board-des h3 {
    font-size: 18px;
    font-weight: bold;
    padding: 20px 0 10px 0;
  }
  .sales-board-des li {
    padding: 5px 0;
  }
  .sales-board-table {
    width: 100%;
    margin-top: 20px;
  }
  .sales-board-table th {
    background: #4fc08d;
    color: #fff;
  }
  .sales-board-table td {
    border: 1px solid #f0f2f5;
    padding: 15px;
  }
</style>



